<template>
	<div class="exercice">
		<p class="exercice__label">{{ primaryExercices[this.id].name }}</p>
		<div class="datas">
			<label class="datas__label">rm</label>
			<input class="datas__input" type="number" @change="calculPerf($event, { from: max.rm, to: max.tm })" :value="roundValue(primaryExercices[this.id].max.rm)">
			<label class="datas__label datas__label--unit">kg</label>
		</div>
		<div class="datas">
			<label class="datas__label">rm</label>
			<input class="datas__input" type="number" @change="calculPerf($event, { from: max.tm, to: max.rm })" :value="roundValue(primaryExercices[this.id].max.tm)">
			<label class="datas__label datas__label--unit">kg</label>
		</div>
	</div>
</template>

<script>

export default {
	name: 'Exercice',
	props: {
		id: String
	},
	data () {
		return {
			max: {
				rm: 'rm',
				tm: 'tm'
			},
			primaryExercices: this.$store.state.primaryExercices
		}
	},
	methods: {
		calculPerf: function(e, {from, to}) {
			this.primaryExercices[this.id].max[from] = parseInt(e.target.value);
			this.primaryExercices[this.id].max[to] = from === this.max.rm ? parseInt(e.target.value) * 0.9 : parseInt(e.target.value) / 0.9;
			this.$store.commit('updatePrimaryExercices', this.primaryExercices);
		},
		roundValue: function(value) {
			return Math.ceil(value * 4) / 4;
		}
	}
}
</script>

<style scoped>
	.exercice {
		margin-top: 30px;
		display: flex
	}

	.exercice__label {
		margin-right: 10px;
		width: 20%;
	}

	.datas {
		display: flex;
		flex: 1;
		border: 1px solid grey;
	}

	.datas + .datas {
		margin-left: 10px;
	}

	.datas__label {
		width: 30px;
		text-align: center;
		background-color: #0e509e;
		color: #fff;
	}

	.datas__label--unit {
		background-color: grey;
	}

	.datas__input {
		width: calc(100% - 60px);
		border: none;
		padding: 0 10px;
		box-sizing: border-box;
	}
</style>


