			<option value="">Week 2</option>
			<option value="">Week 3</option>
		</select>
		<ExerciceLineItem :id="'bench'" />
		<ExerciceLineItem :id="'deadlift'" />
		<ExerciceLineItem :id="'squat'" />
		<ExerciceLineItem :id="'press'" />
	</div>
</template>

<script>
import ExerciceLineItem from '@/components/ExerciceLineItem';

export default {
	name: 'Home',
	components: { ExerciceLineItem },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.home__select {
		margin: 10px 0;
		height: 40px;
	}
</style>



