<template>
	<div>
		<Set 
			v-for="exercice in this.exercices"
			:reps="1"
			:key="exercice.id"
		/>
	</div>
</template>

<script>

import Set from '@/components/Set';

export default {
	name: 'View1',
	components: { Set },
	data () {
		return {
			exercices: ''
		}
	},
	created() {
		this.exercices = this.$store.state.primaryExercices;
	},
	methods: {
	}
}
</script>

<style scoped>

</style>


